@import "@/assets/global.scss";




































































































































.head {
  position: fixed;
  top: 0;
  width: 100%;
  height: 174px;
  background-color: #FCA311;
  z-index: 10;

  > div {
    width: 100%;
    max-width: $max-width;
    padding: 32px;
    box-sizing: border-box;
    text-align: left;
    margin: 0 auto;
  }
}

.barcode {
  z-index: 11;
  position: fixed;
  width: 300px;
  height: 146px;
  top: 94px;
  left: 50%;
  transform: translate(-50%, 0);
  background-color: #FFFFFF;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
  padding: 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.barcode-area {
  z-index: 10;
  position: fixed;
  top: 174px;
  width: 100%;
  height: 80px;
  background: #FAFAFA;
}

.main-list {
  padding: 254px 0 60px 0;
  box-sizing: border-box;
  width: 100%;
  overflow-y: auto;
}

.list-option {
  @extend .font-16-normal;
  position: relative;
  width: 100%;
  max-width: $max-width;
  height: 55px;
  padding: 15px 32px;
  margin: 0 auto;
  box-sizing: border-box;
  text-align: left;

  > img {
    position: absolute;
    right: 32px;
  }
}
